import React, { useState } from "react";
import { Col, Container, Row, Accordion } from "react-bootstrap";
import CustomToggle from "components/shared/CustomToggle";

// needed styles
import styles from "./index.module.scss";
// needed components

import { v4 } from "uuid";
import { useSelector } from "react-redux";
import TripType from "./TripType/TripType";
import SpecialTripType from "./SpecialTripType/SpecialTripType";

const allTripKey = v4();
const umraTripKey = v4();
const hajjTripKey = v4();
const TripsPage = () => {
  const [activeKey, setActiveKey] = useState(allTripKey);

  const { error, loading, umraTrips, allTrips, hajjTrips } =
    useSelector((state) => state.trips) || {};
  const trips = useSelector((state) => state.trips) || {};

  return (
    <Container className={styles["container"]}>
      <Row style={{ justifyContent: "center" }}>
        <Col className={styles.contianer}>
          <Accordion
            defaultActiveKey={allTripKey}
            className={styles.accordionForms}
          >
            <Row className={`${styles.TogglesRow}`}>
              <Col xs={12} sm={3} className={styles.colCell}>
                <CustomToggle
                  eventKey={allTripKey}
                  defaultClass={styles.toggle}
                  children={<>الرحلات</>}
                  alwaysOpen={true}
                  activeKey={activeKey}
                  openClass={styles.activeToggle}
                  setter={() => setActiveKey(allTripKey)}
                />
              </Col>
              <Col xs={12} sm={3} className={styles.colCell}>
                <CustomToggle
                  eventKey={hajjTripKey}
                  openClass={styles.activeToggle}
                  defaultClass={styles.toggle}
                  children={<>رحلات الحج</>}
                  activeKey={activeKey}
                  setter={() => setActiveKey(hajjTripKey)}
                />
              </Col>
              <Col xs={12} sm={3} className={styles.colCell}>
                <CustomToggle
                  eventKey={umraTripKey}
                  openClass={styles.activeToggle}
                  defaultClass={styles.toggle}
                  children={<>رحلات العمره</>}
                  activeKey={activeKey}
                  setter={() => setActiveKey(umraTripKey)}
                />
              </Col>
            </Row>
            <Accordion.Item
              eventKey={allTripKey}
              className={styles.AccordionItem}
            >
              <Accordion.Body>
                <SpecialTripType data={allTrips} />
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item
              eventKey={hajjTripKey}
              className={styles.AccordionItem}
            >
              <Accordion.Body>
                <TripType data={hajjTrips} />
              </Accordion.Body>
            </Accordion.Item>
            <Accordion.Item
              eventKey={umraTripKey}
              className={styles.AccordionItem}
            >
              <Accordion.Body>
                <TripType data={umraTrips} />
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
        </Col>
      </Row>
    </Container>
  );
};

export default TripsPage;
