import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { v4 } from "uuid";
import { Col, Container, Row, Accordion } from "react-bootstrap";
import DefaultCard from "components/shared/DefaultCard/DefaultCard";

// svgs
// import { ReactComponent as Search } from "assets/svgs/search.svg";

// images

// needed styles
import styles from "./index.module.scss";

const TripType = ({ data }) => {
  return (
    <>
      <Row style={{ rowGap: "50px" }} className={styles["programmes"]}>
        {data?.map((card) => {
          const {
            _id: tripId,
            pictures,
            price_per_person,
            title_ar,
            title_en,
            itinerary_title_en,
            itinerary_title_ar,
            isHajj,
          } = card || {};
          return (
            <Col xs={12} lg={4} className={styles["card"]} key={v4()}>
              <DefaultCard
                data={card}
                withButton
                header={title_ar}
                subHeader={itinerary_title_ar}
                title={price_per_person}
                image={pictures[0]}
                tripId={tripId}
                isHajj={isHajj}
              />
            </Col>
          );
        })}
      </Row>
    </>
  );
};

export default TripType;
